

































































































var loginRes, _self;
export default {
	data() {
		return {
			show:false,
			storageData: {
				userId: 0,
				nickname: '',
				avatar: '',
				login: false
			},
			//分类信息
			categories: [{
					cateid: 0,
					name: '文章'
				},
				{
					cateid: 1,
					name: '关注'
				},
				{
					cateid: 2,
					name: '收藏'
				},
				{
					cateid: 3,
					name: '积分'
				}
			],
			// 当前选择的分类
			cateCurrentIndex: 0,
			articles: [],
			follows: [],
			jifen: 100,
			collection: 8
		};
	},
	onLoad: function() {},
	onShow: function() {
		var _this = this;
		uni.request({
			// url: 'http://localhost:8080/api/user/' + uni.getStorageSync('login_key').userId,
			url: this.apiServer + '/user/getUserByMobile',
			method: 'GET',
			data: {
				mobile: _this.mobile
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: res => {
				if (res.data.code === 0) {
					// console.log(res.data.data.avatar + '————————————');
					_this.avatar = res.data.data.avatar;
					_this.nickname = res.data.data.nickname1;
				}
			}
		});
		const loginKey = uni.getStorageSync('login_key');
		if (loginKey) {
			this.login = true;
			// console.log('shihoudengnkw:' + this.login);
			this.storageData = {
				login: loginKey.login,
				nickname: loginKey.nickname,
				avatar: loginKey.avatar,
				userId: loginKey.userId
			};
			// 			uni.request({
			// 				// 获取用户的文章数量
			// 				url: this.apiServer + '/article/getArticleByUID',
			// 				method: 'GET',
			// 				header: { 'content-type': 'application/x-www-form-urlencoded' },
			// 				data: {
			// 					userId: this.storageData.userId
			// 				},
			// 				success: res => {
			// 					_this.articleCount = res.data.data.length;
			// 					console.log('文章数量' + _this.articleCount);
			// 				}
			// 			});
			uni.request({
				// 根据用户id获取所有发表的文章
				url: this.apiServer + '/article/getArticleByUID',
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					uId: this.storageData.userId
				},
				success: res => {
					_this.articles = res.data.data;
				}
			});
			uni.request({
				// 	根据userId查询所有关注的用户
				url: this.apiServer + '/follow/list',
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					fromUId: this.storageData.userId
				},
				success: res => {
					_this.follows = res.data.data;
				}
			});
			uni.request({
				// 	根据userId查询所有收藏的文章
				url: this.apiServer + '/collect/collectlist',
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					fromUId: this.storageData.userId
				},
				success: res => {
					_this.collects = res.data.data;
				}
			});
		} else {
			this.storageData = {
				login: false
			};
		}
	},
	methods: {
		back: function() {
			uni.navigateBack();
		},
		showShade: function() {
			this.show = true;
		},
		closeShade: function() {
			this.show = false;
		},
		tabChange: function(e) {
			// 选中的索引
			var index = e.currentTarget.dataset.index;
			// 具体的分类id
			var cateid = e.currentTarget.dataset.cateid;
			this.cateCurrentIndex = index;
			// 动态替换内容
			this.content = this.categories[index].name;
		},
		gotoDetail: function(aId) {
			uni.navigateTo({
				url: '../articles_detail/article_detail?aId=' +
					aId +
					'&userId=' +
					this.storageData.userId
			});
		},
		mask: function() {
			this.$refs.mask.style.height = doc.getElementById('app').clientHeight + 'px'
		}

	}
};
